.light {
  --black: #202025;
  --white-modal-text: #fcfcfc;
  --grey-theme: #5f6775;
  --grey-modal: #5f6775;
  --orange-modal-line: #fd5103;
  --btn-icon-heart: #fcfcfc;
}

.dark {
  --black: #fcfcfc;
  --white-modal-text: #202025;
  --grey-theme: #fcfcfc;
  --grey-modal: #bdbdbd;
  --orange-modal-line: #202025;
  --btn-icon-heart: #202025;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
}

.page {
  margin: 0;
  font-family: Poppins, sans-serif;
}

.is-hidden {
  visibility: 0;
  opacity: 0;
  pointer-events: none;
}

body {
  height: 100vh;
  flex-direction: column;
  display: flex;
}

body > * {
  flex-shrink: 0;
}

main {
  flex-grow: 1;
}

footer {
  height: 110px;
  width: 100%;
