.header__switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header__switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.header__slider {
  cursor: pointer;
  background-color: #fcfcfc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.header__slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #fd5103;
  transition: all .4s;
  position: absolute;
  bottom: 1.5px;
  left: 1px;
}

input:checked + .header__slider {
  background-color: #fd5103;
}

input + .header__slider {
  border: .604701px solid #fd5103;
  border-radius: 20px;
}

input:checked + .header__slider:before {
  background-color: #fcfcfc;
  border: .604701px solid #fcfcfc;
  transform: translateX(20px);
}

.header__slider.header__round:before {
  border-radius: 50%;
}

